
import { defineComponent } from "vue";
import useAuthUser from "../composables/useAuthUser";

export default defineComponent({
    setup(props, { emit }) {
        const showAllProducts = () =>{
            emit("showAll")
        }
        const { deleteToken } = useAuthUser();
        const logout = () => {
            deleteToken();
        }

        return { 
            showAllProducts,
            logout,
        }
    },
})
